body {
  color: #fff;
  background: linear-gradient(#0a0a0a 0%, #111 100%);
  margin: 0;
  font-family: Poppins, sans-serif;
  overflow-x: hidden;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 45px 0;
}

section {
  margin-bottom: 100px;
  position: relative;
}

section:before {
  content: attr(class);
  text-transform: uppercase;
  color: #00b4ff;
  text-align: center;
  letter-spacing: 3px;
  text-shadow: 0 0 15px #00b4ff80;
  margin-bottom: 35px;
  font-size: 2.5rem;
  font-weight: 700;
  animation: 1s ease-in-out fadeInDown;
  display: block;
}

.football-list, .stadium-list {
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.football-list li, .stadium-list li {
  background: linear-gradient(145deg, #1c1c1c, #262626);
  border-radius: 20px;
  transition: all .5s;
  animation: 1s forwards fadeIn;
  position: relative;
  overflow: hidden;
  transform: scale(1);
  box-shadow: 0 8px 25px #00000080;
}

.football-list img, .stadium-list img {
  object-fit: cover;
  border-bottom: 2px solid #00b4ff;
  width: 100%;
  height: 220px;
  transition: transform .6s, filter .6s;
}

.football-list li:hover img, .stadium-list li:hover img {
  filter: brightness(1.2) saturate(1.1);
  transform: scale(1.1)rotate(1deg);
}

.football-list li:hover, .stadium-list li:hover {
  transform: translateY(-10px)rotate(-.5deg);
  box-shadow: 0 15px 30px #00b4ff66;
}

.card-content {
  padding: 20px;
}

.card-content h3 {
  color: #00b4ff;
  margin: 12px 0 8px;
  font-size: 1.5rem;
  transition: color .3s;
}

.card-content h3:hover {
  color: gold;
}

.card-content p {
  color: #ccc;
  margin: 5px 0;
  font-size: .95rem;
  line-height: 1.4;
}

.card-content span {
  color: gold;
  letter-spacing: 1px;
  margin-top: 8px;
  font-weight: 600;
  display: block;
}

.rating {
  color: gold;
  background: #ffd70026;
  border-radius: 12px;
  margin-top: 12px;
  padding: 8px 16px;
  font-weight: bold;
  animation: 2s ease-in-out infinite pulse;
  display: inline-block;
}

.button {
  color: #0a0a0a;
  text-transform: uppercase;
  cursor: pointer;
  background: linear-gradient(90deg, #00b4ff, #00ffea);
  border-radius: 25px;
  padding: 10px 25px;
  font-weight: 700;
  text-decoration: none;
  transition: all .4s;
  display: inline-block;
  box-shadow: 0 6px 20px #00b4ff80;
}

.button:hover {
  transform: scale(1.05)translateY(-3px);
  box-shadow: 0 10px 30px #00b4ffb3;
}

section.stadium:after {
  content: "";
  z-index: 0;
  background: radial-gradient(circle, #00b4ff33 0%, #0000 70%);
  border-radius: 50%;
  width: 300px;
  height: 300px;
  animation: 6s ease-in-out infinite float;
  position: absolute;
  top: -50px;
  right: -50px;
}

.football-list li, .stadium-list li {
  border: 2px solid #00b4ff80;
  transition: all .5s;
  box-shadow: 0 0 20px #00b4ff4d, 0 0 40px #00b4ff33, inset 0 0 10px #00b4ff33;
}

.football-list li:hover, .stadium-list li:hover {
  border-color: #00ffea;
  box-shadow: 0 0 30px #00ffea, 0 0 60px #00ffea66, inset 0 0 15px #00ffea4d;
}

.card-content h3 {
  text-shadow: 0 0 5px #00b4ff, 0 0 10px #00b4ff, 0 0 20px #00ffea;
}

.card-content p {
  text-shadow: 0 0 2px #00b4ff;
}

.card-content .icons {
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.card-content .icons i {
  color: #00b4ff;
  font-size: 1.2rem;
  transition: transform .3s, color .3s;
}

.card-content .icons i:hover {
  color: gold;
  text-shadow: 0 0 8px gold;
  transform: scale(1.3)rotate(-10deg);
}

.football-list li:before, .stadium-list li:before {
  content: "";
  z-index: 0;
  background: radial-gradient(circle, #00ffea33 0%, #0000 70%);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: 6s ease-in-out infinite floatCard;
  position: absolute;
  top: -20px;
  right: -20px;
}

.football-list li:after, .stadium-list li:after {
  content: "";
  z-index: 0;
  background: radial-gradient(circle, #00b4ff26 0%, #0000 70%);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 5s ease-in-out infinite reverse floatCard;
  position: absolute;
  bottom: -15px;
  left: -15px;
}

@keyframes floatCard {
  0%, 100% {
    transform: translateY(0)rotate(0);
  }

  50% {
    transform: translateY(15px)rotate(5deg);
  }
}

.button {
  box-shadow: 0 0 10px #00b4ff, 0 0 20px #00ffea, 0 0 40px #00ffea80;
}

.button:hover {
  box-shadow: 0 0 20px #00ffea, 0 0 40px #00ffea, 0 0 60px #00ffeab3;
}

.rating {
  animation: 1.8s infinite alternate neonPulse;
}

@keyframes neonPulse {
  0% {
    box-shadow: 0 0 10px gold, 0 0 20px gold;
  }

  50% {
    box-shadow: 0 0 20px gold, 0 0 40px gold, 0 0 60px #ffd70080;
  }

  100% {
    box-shadow: 0 0 10px gold, 0 0 20px gold;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0)rotate(0);
  }

  50% {
    transform: translateY(20px)rotate(5deg);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 10px #ffd70080;
  }

  50% {
    box-shadow: 0 0 25px gold;
  }
}
